import { ValueUpdateDetail } from '../../utils/UNSAFE_valueUpdateDetail';
import { Item } from '../../utils/UNSAFE_dataProvider';
import type { RootEnvironment } from '../../UNSAFE_Environment';
import { RootEnvironmentProvider } from '../../UNSAFE_Environment';
import { matchTranslationBundle } from '../../utils/UNSAFE_matchTranslationBundle';

afterEach(cleanup);

const valueItems = [
  { key: 'CH', data: { value: 'CH', label: 'Chrome' }, metadata: { key: 'CH' } },
  { key: 'FF', data: { value: 'FF', label: 'Firefox' }, metadata: { key: 'FF' } }
];
const valueItems2 = [{ key: 'OP', data: { value: 'OP', label: 'Opera' }, metadata: { key: 'OP' } }];

const noOp = () => {};

type Key = string;
type Data = {
  value: string;
  label: string;
};
const items: Data[] = [
  { value: 'IE', label: 'Internet Explorer' },
  { value: 'FF', label: 'Firefox' },
  { value: 'CH', label: 'Chrome' },
  { value: 'OP', label: 'Opera' },
  { value: 'SA', label: 'Safari' }
];
const keyAttr = 'value';

type PickedPropsFromSelectMultiple = Pick<
  ComponentProps<typeof SelectMultiple>,
  | 'assistiveText'
  | 'helpSourceLink'
  | 'helpSourceText'
  | 'isDisabled'
  | 'isReadonly'
  | 'isRequired'
  | 'label'
  | 'labelEdge'
  | 'messages'
  | 'placeholder'
  | 'textAlign'
  | 'userAssistanceDensity'
  | 'valueItems'
>;
type DemoSelectMultipleProps = PickedPropsFromSelectMultiple & {
  data?: Data[];
};
function DemoSelectMultiple({
  assistiveText,
  data = items,
  helpSourceLink,
  helpSourceText,
  isDisabled,
  isReadonly,
  isRequired,
  label,
  labelEdge,
  messages,
  placeholder,
  textAlign,
  userAssistanceDensity,
  valueItems
}: DemoSelectMultipleProps) {
  const [myValues, setMyValues] = useState<Set<Key> | undefined>();
  const [myValueItems, setMyValueItems] = useState(valueItems);
  const handleCommit = useCallback(
    (detail: ValueUpdateDetail<Set<Key>>) => {
      if (myValues != detail.value) {
        setMyValues(detail.value);

        const newValueItems: Item<Key, Data>[] = [];
        detail.value?.forEach((key) => {
          const item = data?.find((element) => element[keyAttr] === key);
          if (item) {
            newValueItems.push({
              key: item[keyAttr],
              data: item,
              metadata: { key: item[keyAttr] }
            });
          }
        });
        setMyValueItems(newValueItems.length > 0 ? newValueItems : undefined);
      }
    },
    [myValues]
  );

  const dataMapFunc = (value: any) => {
    return { data: value, metadata: { key: value[keyAttr] } };
  };

  const dataMetadataRef = useRef(data?.map(dataMapFunc));

  const [dataState, setDataState] = useState<DataState<string, Data> | null>({
    offset: 0,
    totalSize: dataMetadataRef.current?.length ?? 0,
    sizePrecision: 'exact',
    data: dataMetadataRef.current.slice(0, dataMetadataRef.current.length)
  });

  const [filter, setFilter] = useState('');

  const handleFilter = useCallback(({ searchText }: { searchText?: string }) => {
    setFilter(searchText ?? '');
  }, []);

  const delay = 0;

  // this effect should only be run when filter has changed
  const firstRender = useRef(true);
  useEffect(() => {
    if (firstRender.current) {
      firstRender.current = false;
      return;
    }

    // put ListView into loading state
    setDataState(null);

    setTimeout(() => {
      // figure out what data to show
      const dataToShow =
        filter != null && filter.trim().length > 0
          ? data?.filter((value: Data) => {
              return value.label.toLowerCase().indexOf(filter.toLowerCase()) > -1;
            })
          : data;
      dataMetadataRef.current = dataToShow?.map(dataMapFunc);

      // update DataState to show the first 25 results
      loadRange({ offset: 0, count: 25 });
    }, delay);
  }, [filter]);

  const loadRange = (range: Range) => {
    const startIndex = range.offset;
    const endIndex = startIndex + range.count;
    const newDataState: DataState<string, Data> = {
      offset: startIndex,
      totalSize: dataMetadataRef.current?.length ?? 0,
      sizePrecision: 'exact',
      data: dataMetadataRef.current.slice(startIndex, endIndex)
    };
    setDataState(newDataState);
  };

  return (
    // <SelectMultiple<Key, Data>
    <SelectMultiple
      assistiveText={assistiveText}
      data={dataState}
      helpSourceLink={helpSourceLink}
      helpSourceText={helpSourceText}
      isDisabled={isDisabled}
      isReadonly={isReadonly}
      isRequired={isRequired}
      itemText="label"
      label={label}
      labelEdge={labelEdge}
      messages={messages}
      onCommit={handleCommit as any}
      onFilter={handleFilter}
      onLoadRange={loadRange}
      placeholder={placeholder}
      textAlign={textAlign}
      userAssistanceDensity={userAssistanceDensity}
      valueItems={myValueItems as any}></SelectMultiple>
  );
}

async function getEnvironmentContext() {
  const locale = matchTranslationBundle(['en'], new Set(['en-US', 'en']));
  const { default: translations } = await import(`../../resources/nls/${locale}/bundle.ts`);
  return {
    translations: { '@oracle/oraclejet-preact': translations }
  } as Partial<RootEnvironment>;
}

