  // The below tests are for:
  // JET-53183 - core pack compat: stop propagation of handled events
  // SelectMultiple stops keydown events from propagating when it has handled them so that a parent
  // collection component won't also handle them.

  it('JET-53183 - propagation not stopped for Enter keydown in input', async function () {
    const env = await getEnvironmentContext();
    let keyDownReceived = false;
    const handleKeyDown = () => {
      keyDownReceived = true;
    };
    const getComp = (valItems: any) => (
      <RootEnvironmentProvider environment={env}>
        <div onKeyDown={handleKeyDown}>
          <DemoSelectMultiple
            data={items}
            label="Enabled"
            labelEdge="inside"
            valueItems={valItems}
          />
        </div>
      </RootEnvironmentProvider>
    );
    const content = render(getComp(valueItems));

    const input = await content.findByRole('combobox');
    expect(input, 'input element found').not.null;

    fireEvent.keyDown(input, { code: KEYS.ENTER });

    expect(keyDownReceived, 'propagation not stopped').to.be.true;
  });

